import { createSlice } from '@reduxjs/toolkit';
import { updateObjectInArray } from '../../utils/helpers/updateObjectInArray';

const usersSlice = createSlice({
  name: 'users',
  initialState: {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: [],
  },
  reducers: {
    setUsers(state, action) {
      return {
        ...state,
        users: action.payload,
      };
    },
    setTotalUsersCount(state, action) {
      return {
        ...state,
        totalUsersCount: action.payload,
      };
    },
    setCurrentPage(state, action) {
      return {
        ...state,
        currentPage: action.payload,
      };
    },
    follow(state, action) {
      return {
        ...state,
        users: updateObjectInArray(state.users, action.payload, 'id', {
          followed: true,
        }),
        // users: state.users.map((u) => {
        //   //  ID   state === ID  action.payload
        //   if (u.id === action.userId) {
        //     //    ,   
        //     return { ...u, followed: true };
        //   }
        //   return u;
        // }),
      };
    },
    unfollow(state, action) {
      return {
        ...state,
        users: updateObjectInArray(state.users, action.payload, 'id', {
          followed: false,
        }),
      };
    },
    toggleIsFetching(state, action) {
      return {
        ...state,
        isFetching: action.payload,
      };
    },
    toggleFollowingInProgress(state, action) {
      const { isFetching, id } = action.payload;
      
      return {
        ...state,
        //     follow/unfollow   (id)   (disabled)
        //  id   ,       
        //  id   ;
        followingInProgress: isFetching
          ? //   (isFetching) === true () =>  ID   ,
            //       ( ID)
            [...state.followingInProgress, id]
          : //   (isFetching) === false () =>  ID   
            state.followingInProgress.filter((id) => id !== action.payload.id),
      };
    },
  },
});

export const {
  setUsers,
  setTotalUsersCount,
  setCurrentPage,
  follow,
  unfollow,
  toggleIsFetching,
  toggleFollowingInProgress,
} = usersSlice.actions;
export default usersSlice.reducer;
